@import url(https://fonts.googleapis.com/css2?family=Cookie&family=Dosis&family=Estonia&display=swap);
.cart-btn {
  display: flex;
  width: 70px;
  justify-content: center;
  align-items: center;
  padding: 0.2rem 0.1rem;
  border: 1px solid black;
  border-radius: 3px;
}

.cart-btn:hover {
  background-color: rgb(212, 211, 209);
}

.cart-icon,
.cart-item-num {
  margin: 0;
}

.cart-icon {
  width: 50%;
}

.cart-icon img {
  width: 70%;
  height: auto;
}
.nav {
  margin: 0 1rem;
}

.nav ul {
  display: flex;
  align-items: center;
}

.nav li {
  margin: 0 1rem;
}

.nav a {
  text-decoration: none;
  color: black;
}

.nav li:hover {
  font-weight: bold;
  transform: scale(1.05);
}
.header {
  display: flex;
  height: 70px;
  border-bottom: 1px solid black;
  justify-content: space-between;
  align-items: center;
  background-color: rgb(250, 244, 237);
}

.header h1 {
  font-size: 3rem;
  font-family: 'Cookie', cursive;
  margin: 0 1rem;
}

h2 {
  font-family: 'Cookie', cursive;
}
.home-container {
  text-align: center;
  margin: 0 auto;
  width: 50%;
}

.home-container h2 {
  font-family: 'Cookie', cursive;
  font-size: 2.5rem;
  margin: 0.5rem 0 1rem 0;
  text-decoration: underline;
  text-decoration-thickness: 0.15rem;
  text-underline-offset: 0.2rem;
}

.home-container section {
  width: 100%;
  padding: 1rem;
  margin: 3rem 0;
  background-color: rgb(250, 244, 236);
  border: 1px solid grey;
  border-radius: 5px;
  box-shadow: 1px 1px 1px 1px rgb(221, 219, 219);
}

.home-container section p {
  font-family: 'Dosis', sans-serif;
  font-size: 1.1rem;
  margin-bottom: 1rem;
}

.emphasis {
  display: block;
  font-family: 'Estonia', cursive;
  font-size: 3rem;
  margin-bottom: 2rem;
}

.mission .emphasis {
  margin-top: 0rem;
}

.mission p {
  font-size: 1.1rem;
  line-height: 1.6;
  padding: 0.5rem;
}

.testimonials iframe {
  margin-bottom: 1rem;
}

.home-shop-link {
  cursor: pointer;
  font-family: 'Dosis', sans-serif;
  font-size: 1.5rem;
  background-color: rgb(250, 190, 205);
  padding: 0.6rem;
  border: 1px solid black;
  border-radius: 5px;
}

.home-shop-link:hover {
  background-color: rgb(228, 161, 178);
  transform: scale(1.05);
}

.qty-controller {
  display: flex;
  width: 100px;
  margin: 0 auto 0.4rem auto;
}

.qty-controller button {
  width: 25%;
  border: 1px solid black;
  font-size: 1rem;
}

.qty-controller button:hover {
  cursor: pointer;
  background-color: rgb(196, 196, 196);
  font-weight: bold;
}

.qty-controller input {
  width: 50%;
  text-align: center;
  border-left: none;
  border-right: none;
  border-top: 1px solid black;
  border-bottom: 1px solid black;
}

input[type=number]::-webkit-outer-spin-button,
input[type=number]::-webkit-inner-spin-button {
  display: none;
  -webkit-appearance: none;
  margin: 0;
}

.shop-item {
  display: flex;
  flex-direction: column;
  padding: 1rem;
  border: 1px solid black;
  border-radius: 5px;
  margin: 2rem;
  background-color: rgb(250, 244, 237);
}

.photo-container {
  width: 300px;
  height: 300px;
}

.photo-container img {
  width: 100%;
  height: auto;
  border-radius: 5px;
}

.item-order-info {
  text-align: center;
  margin-top: 1rem;
}

.item-order-info h2,
.item-order-info p,
.add-to-cart {
  font-family: 'Dosis', sans-serif;
}

.item-order-info h2 {
  font-size: 1.5rem;
  font-weight: bold;
  margin-bottom: 0.4rem;
}

.item-order-info p {
  font-size: 1.3rem;
  margin-bottom: 0.4rem;
}

.add-to-cart {
  cursor: pointer;
  font-size: 1rem;
}
.loading-icon {
  width: 150px;
  height: auto;
  margin: 1rem;
  animation: rotation 8s infinite linear;
}

@keyframes rotation {
  from {
    transform: rotate(0deg)
  }
  to {
    transform: rotate(359deg)
  }
}
.shop-container {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  align-items: center;
  width: 70%;
  margin: 0 auto;
}
.cart-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border: 1px solid black;
  border-radius: 5px;
  width: 90%;
  padding: 0.5rem;
  margin: 0.5rem auto 0.5rem auto;
  background-color: rgb(250, 246, 246);
}

.thumbnail-container {
  width: 100px;
  height: 100px;
}

.thumbnail-container img {
  width: 100%;
  height: auto;
  border-radius: 5px;
}

.cart-item-info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-family: 'Dosis', sans-serif;
}

.cart-item-info .qty-controller {
  margin-bottom: 0;
}

.cart-item-name,
.cart-item-price-total {
  font-weight: bold;
}

.cart-item-name {
  font-size: 1.2rem;
  margin-bottom: 0.5rem;
}

.cart-item-price {
  margin-bottom: 0.3rem;
}

.cart-item-price-total {
  margin-top: 0.5rem;
  border-top: 1px solid black;
}

.trash-icon {
  width: 20px;
  height: auto;
}

.trash-icon:hover {
  cursor: pointer;
  transform: scale(1.05);
}
.cart-container {
  display: flex;
  flex-direction: column;
  width: 50%;
  padding: 1rem;
  margin: 1rem auto;
  border: 1px solid black;
  border-radius: 5px;
  background-color: rgb(250, 244, 237);
  text-align: center;
}

.cart-container > p,
.cart-container > button {
  font-family: 'Dosis', sans-serif;
}

.total-price {
  font-weight: bold;
  font-size: 1.4rem;
  margin-top: 1rem;
}

.empty-cart-text {
  font-size: 1.5rem;
}

.checkout,
.cart-shop-btn {
  cursor: pointer;
  width: 100px;
  font-size: 1.2rem;
  margin: 1rem auto 0.5rem auto;
}

.cart-shop-btn {
  font-family: 'Dosis', sans-serif;
}

/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
html {
  font-size: 16px;
}

body {
  background-color: rgb(250, 246, 246);
}
